import http from "comp/http-common";

/**
 *  : .
 *   : .
 *   : .
 *  :    
 */
class MainService {
    /**
     *  
     * @param {String} id 
     * @param {String} pw 
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    login(id,pw,resFn,errFn) {
        let formData = new FormData();
        formData.append("username",id);
        formData.append("password",pw);
        return http.post("/login",formData)
            .then(function (res) {
                if (resFn) {
                    resFn(res.data);
                }
            })
            .catch(function (err) {
                if (errFn) {
                    errFn(err);
                }
            });
    }
    
    /**
     *  
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    logout(resFn, errFn) {
        return http.get("/logout").then(function(res) {
            if (resFn) {
                resFn(res.data);
            }
        }).catch(function(err) {
            if (errFn) {
                errFn(err);
            }
        })
    }
    
    /**
     *    
     * @param {String} sessionId  
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    chkLogin(sessionId, resFn, errFn) {
        let formData = new FormData();
        formData.append("sessionId",sessionId);
        return http.post("/chkLogin",formData)
            .then(function(res) {
                if (resFn) {
                    resFn(res.data);
                }
            }).catch(function(err) {
                if (errFn) {
                    errFn(err);
                }
            });
    }
    
    /**
     *   
     * @param {Object} parm 
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    signUp(parm, resFn, errFn) {
        let formData = new FormData();
        for (let v in parm) {
            formData.append(v,parm[v]);
        }
        return http.post("/signup",formData)
            .then(function (res) {
                if (resFn) {
                    resFn(res.data);
                }
            })
            .catch(function (err) {
                if (errFn) {
                    errFn(err);
                }
            });
    }
    
    /**
     *    
     * @param {Object} parm 
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    chkId(parm, resFn, errFn) {
        let formData = new FormData();
        for (let v in parm) {
            formData.append(v,parm[v]);
        }
        return http.post("/chkId",formData)
            .then(function (res) {
                if (resFn) {
                    resFn(res.data);
                }
            })
            .catch(function (err) {
                if (errFn) {
                    errFn(err);
                }
            });
    }
    
    /**
     *   
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    commonCodeList(resFn, errFn) {
        let parm = {groupUseYn:'Y',useYn:'Y'};
        let formData = new FormData();
        for (let v in parm) {
            formData.append(v,parm[v]);
        }
        return http.post("/code/list",formData)
            .then(function (res) {
                if (resFn) {
                    resFn(res.data);
                }
            })
            .catch(function (err) {
                if (errFn) {
                    errFn(err);
                }
            });
    }
    
    /**
     *   
     * @param {Object} parm 
     * @param {Function} resFn   
     * @param {Function} errFn   
     */
    changeUserPw(parm,resFn,errFn) {
        let formData = new FormData();
        for (let v in parm) {
            formData.append(v,parm[v]);
        }
        return http.post("/changeUserPw",formData)
            .then(function (res) {
                if (resFn) {
                    resFn(res.data);
                }
            })
            .catch(function (err) {
                if (errFn) {
                    errFn(err);
                }
            });
	}
}

export default new MainService();