import React, {Component} from 'react';
import { Msg, Validation } from 'comp';
import { MainService } from 'main';
import { makeParam } from 'comp/common';

/**
 *  :  
 *   :       
 *   : .
 *  : 
 */
class ChangeUserPassword extends Component {
    constructor(props) {
        super(props);
        this.onClickReg = this.onClickReg.bind(this);
        this.onConfirmOk = this.onConfirmOk.bind(this);
        this.onConfirmCancel = this.onConfirmCancel.bind(this);
        this.validationCheck = this.validationCheck.bind(this);
    }

    componentDidMount() {
        this.refs.currpw.focus();
    }

	/**************************************************************************/
	/*  */
	/**************************************************************************/
    /**
     *    
     */
    onClickReg() {
        if (!this.validationCheck()) return;
        //  ?
        const popTitle = "Change User Password Confirm";
        this.props.main.showPopup(
            popTitle,
            <div style={{textAlign: "center",fontSize: 17,paddingTop:4,paddingBottom: 21}}> ?</div> ,
            {width:432, height:171, headerVisiable:false},this.onConfirmOk,this.onConfirmCancel
        );
    }
    
    /**
     *   
     */
    validationCheck() {
        // 
        let chkVal = this.refs.currpw.value;
        if (chkVal.length < 1) {
            Msg.error("   .");
            return false;
        }
        chkVal = this.refs.pw.value;
        if (chkVal.length < 1) {
            Msg.error("   .");
            return false;
        }
        let valiRes = Validation.chkPw(chkVal);
        if (!valiRes.result) {
            Msg.error(valiRes.errMsg);
            return false;
        }
        let chkVal2 = this.refs.pwchk.value;
        if (chkVal !== chkVal2) {
            Msg.error("  .");
            return false;
        }
        return true;
    }

	/**************************************************************************/
	/*  */
	/**************************************************************************/
    /**
     *  
     */
    onConfirmOk() {
        // Msg.info("ok");
        const parm = makeParam(this);
        // console.log(parm);
        parm.userId = this.props.main.state.userInfo.userId;
        MainService.changeUserPw(parm,this.onResultChangePw,(e) => Msg.error(e.message));
    }
    
    /**
     *    
     * @param {Object} res {result:ok|fail,message: }
     */
    onResultChangePw(res) {
        if (res.result === "ok") {
            Msg.ok(res.message);
            this.props.onClose();
        } else if (res.result === "fail") {
            Msg.error(res.message);
        }
    }
    
    /**
     *   
     */
    onConfirmCancel() {
        // Msg.info("cancel");
    }
    
    render() {
        return <div className="table_type2">
            <table>
                <tbody>
                <tr>
                    <th>Password</th>
                    <td>
                    <div className="ipt_group">
                        <input type="password" className="ipt_text" ref="currpw" placeholder=" "/>
                        <input type="password" className="ipt_text" ref="pw" placeholder=",  4 12" style={{marginTop: 4}}/>
                        <input type="password" className="ipt_text" ref="pwchk" placeholder="" style={{marginTop: 4}}/>
                    </div>
                    </td>
                </tr>
                </tbody>
            </table>
            <div className="popupBtnArea btn_area addbtnarea"><p className="btn2">
                <button className="btn btn_white" onClick={() => this.props.onClose()}></button>
                <button className="btn btn_black" onClick={() => this.onClickReg()}></button>
            </p></div>
        </div>;
    }
}

export default ChangeUserPassword;