import React, {Component} from 'react';
import { AdminService } from 'admin';
import Validation from 'comp/Validation';
import { Msg } from 'comp';

/**
 *  :  /  
 *   :
 *   :
 *  : 
 */
class SchIdPw extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tabFocus:"schId"
        };
        this.tabChange = this.tabChange.bind(this);
        this.onClickSchId = this.onClickSchId.bind(this);
        this.onClickSchPw = this.onClickSchPw.bind(this);
        this.validationSchId = this.validationSchId.bind(this);
        this.validationSchPw = this.validationSchPw.bind(this);
        this.onResultSearchId = this.onResultSearchId.bind(this);
        this.onResultSearchPw = this.onResultSearchPw.bind(this);
    }

	/**************************************************************************/
	/*  */
	/**************************************************************************/
    /**
     *  
     * @param {String} type   (schId|schPw)
     */
    tabChange(type) {
        this.setState({tabFocus:type});
    }
    
    /**
     *   
     */
    onClickSchId() {
        if (!this.validationSchId()) return;
        let parm = {};
        parm.userName = this.refs.userName.value;
        parm.userEmail = this.refs.schIdEmail.value;
        // console.log("searchId parameter >> ");
        // console.log(parm);
        AdminService.searchId(parm,this.onResultSearchId,(e) => console.log(e));
    }
    
    /**
     *   
     */
    onClickSchPw() {
        if (!this.validationSchPw()) return;
        let parm = {};
        parm.userId = this.refs.userId.value;
        parm.userEmail = this.refs.schPwEmail.value;
        // console.log("searchPw parameter >> ");
        // console.log(parm);
        AdminService.searchPw(parm,this.onResultSearchPw,(e) => console.log(e));
    }
    
    /**
     *   Validation Check
     * @return {boolean} 
     */
    validationSchId() {
        //userName
        //schIdEmail
        let chkVal = this.refs.userName.value;
        if (chkVal.length < 2) {
            Msg.error("  .");
            this.refs.userName.focus();
            return false;
        }
        chkVal = this.refs.schIdEmail.value;
        let valiRes = Validation.chkEmail(chkVal);
        if (!valiRes.result) {
            Msg.error(valiRes.errMsg);
            this.refs.schIdEmail.focus();
            return false;
        }
        return true;
    }
    
    /**
     *   Validation Check
     * @return {boolean} 
     */
    validationSchPw() {
        //userId
        //schPwEmail
        let chkVal = this.refs.userId.value;
        if (chkVal.length < 2) {
            Msg.error("  .");
            this.refs.userId.focus();
            return false;
        }
        chkVal = this.refs.schPwEmail.value;
        let valiRes = Validation.chkEmail(chkVal);
        if (!valiRes.result) {
            Msg.error(valiRes.errMsg);
            this.refs.schPwEmail.focus();
            return false;
        }
        return true;
    }

	/**************************************************************************/
	/*  */
	/**************************************************************************/
    /**
     *    
     * @param {Object} res {result:ok|fail,data:{userId},message: }
     */
    onResultSearchId(res) {
        console.log(res);
        if (res.result === "ok") {
            Msg.ok(" " + res.data.userId + ".");
        }
        else if (res.result === "fail") {
            Msg.error(res.message);
        }
    }
    
    /**
     *    
     * @param {Object} res {result:ok|fail,message: }
     */
    onResultSearchPw(res) {
        console.log(res);
        if (res.result === "ok") {
            Msg.ok(res.message);
        }
        else if (res.result === "fail") {
            Msg.error(res.message);
        }
    }
    
    render() {
        const {tabFocus} = this.state;
        return <div className="schIdPw mainContent"><div className={'container'}>
            <div className="tab">
                <div className={"col-md-6 item" + (tabFocus === "schId" ? " active" : "")} onClick={() => (tabFocus !== "schId" ? this.tabChange('schId') : null)}>ID </div>
                <div className={"col-md-6 item" + (tabFocus === "schPw" ? " active" : "")} onClick={() => (tabFocus !== "schPw" ? this.tabChange('schPw') : null)}>Password </div>
            </div>

            {tabFocus === "schId"
                ? <div className="form">
                    <div className="form">
                        <input ref="userName" className="form-control" placeholder="" />
                    </div>
                    <div className="form">
                        <input ref="schIdEmail" className="form-control" placeholder=""/>
                    </div>
                    <div className="form">
                        <button className="btn btn-success btn-sm" onClick={() => this.onClickSchId()}> </button>
                    </div>
                </div>
            : null}
            {tabFocus === "schPw"
                ? <div className="form">
                <div className="form">
                    <input ref="userId" className="form-control" placeholder="" />
                </div>
                <div className="form">
                    <input ref="schPwEmail" className="form-control" placeholder=""/>
                </div>
                <div className="form">
                    <button className="btn btn-success btn-sm" onClick={() => this.onClickSchPw()}>Password </button>
                </div>
            </div>
        : null}
        </div></div>;
    }
}

export default SchIdPw;